* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
    
  }

  img {
    display: block;
    width: 100%;
    height: auto;
    
    height: 200px;
  }
  
  form {    
    width: 100%;
    background-color: #5858FA;
    

  }
  .serch-box {
    position: relative;
    width: 300px;
    margin: 0 auto;
  }
  input {
    width: 100%;
    height: 42px;
    padding-left: 10px;
    border: 2px solid #5858FA;
    border-radius: 5px;
    outline: none;
    color: #000000;
  }
  button {
    position: absolute; 
    top: 0;
    right: 0px;
    width: 42px;
    height: 42px;
    border-color: #5858FA;
    border-left: none;
    background: #ffffff;
    cursor: pointer;
  }
  button:before {
    content: "\f002";
    font-family: FontAwesome;
    font-size: 16px;
    color: #000000;
  }

  .gallery {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  .photo-card {
    width: calc((100% - 30px) / 4);
    margin-right: 10px;
    margin-bottom: 10px;
     
  }

  .photo-card:nth-child(4n + 4) {
    margin-right: 0px;
  }

  .info {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    

    

  }

  .info-item {
    font-size: 12px;
    display: flex;
    flex-direction: column;    
    align-items: center;
    
    
    
  }

  .info-item:nth-child(4) {
    padding-right: 0px;
  }